<template>
<div class="vue" v-show="info.headerImg">

    <div class="goodsHeader">
        <img :src="info.headerImg">
        <div class="box">
            <div class="goodsName">{{info.name}}</div>
            <div class="count">{{info.num}}</div>
            <div class="price">{{info.price}}</div>
            <div class="tagBox">
                <div class="tag">无理由退换</div>
                <div class="tag">可抵扣</div>
                <div class="tag">需现场调音</div>
            </div>
        </div>
    </div>

    <div class="main">
        <img :src="info.detailImg" alt="">
    </div>

    <!-- <div class="online-pay" @click="pay">在线支付</div> -->

</div>
</template>

<script>
import {
    Indicator
} from 'mint-ui';


export default {
    data() {
        return {
            info: {}
        }
    },
    mounted() {

        let session_goods = window.window.sessionStorage.goods;
        let id = this.$route.params.id; //query string
        let that = this;
        if (session_goods) {
            let oGoods = JSON.parse(session_goods);
            oGoods.forEach((item, index) => {
                console.log(item);
                if (item.id === id) {
                    that.info = item;
                    that.findCache = true;
                }
            });

        }
        if (!this.findCache) {
            Indicator.open({
                spinnerType: 'triple-bounce'
            });
            this.getInfo(id);
        }
    },
    methods: {
        getInfo(id) {
            let that = this;
            Indicator.open({
                spinnerType: 'triple-bounce'
            });
            //mock ajax
            setTimeout(() => {
                Indicator.close();
                let info = {
                    id: id,
                    headerImg: 'https://ss2.bdstatic.com/70cFvnSh_Q1YnxGkpoWK1HF6hhy/it/u=454000548,4150943646&fm=23&gp=0.jpg',
                    name: '琴瑟古琴',
                    num: '数量一台',
                    price: '￥300.00',
                    detailImg: 'https://ss1.bdstatic.com/70cFuXSh_Q1YnxGkpoWK1HF6hhy/it/u=3737536382,4184817415&fm=23&gp=0.jpg',
                };

                Indicator.close();
                that.info = info;
                let cacheStorage = window.window.sessionStorage.goods;
                if (cacheStorage) {
                    let arr = JSON.parse(cacheStorage);
                    arr.push(info);
                    window.window.sessionStorage.goods = JSON.stringify(arr); //
                } else {
                    window.window.sessionStorage.goods = "[" + JSON.stringify(info) + "]";
                }
            }, 2000);
        },
        goTeacher(el) {
            let id = el.target.parentNode.getAttribute('data-id');
            console.log(this.$router);
            this.$router.push({
                path: 'teacher/' + id
            })
        }
    }
}
</script>

<style lang="scss" scoped>
img {
    display: block;
}
.goodsHeader {
    background: #fff;
    img {
        height: 2.5rem;
    }
    .box {
        height: 1.17rem;
        background: #fff;
        text-align: center;
        .goodsName {
            padding-top: 0.1rem;
            line-height: 0.27rem;
            font-size: 0.17rem;
            color: #333;
        }
        .count {
            line-height: 0.22rem;
            font-size: 0.12rem;
            color: #666;
        }
        .price {
            line-height: 0.26rem;
            font-size: 0.16rem;
            color: #b4282d;
        }
        .tagBox {
            padding-top: 0.1rem;
            width: 2.76rem;
            margin: 0 auto;
            font-size: 0;
            .tag {
                margin: 0 0.15rem;
                width: 0.6rem;
                border-radius: 0.02rem;
                border: 0.01rem solid #bd8747;
                color: #bd8747;
                display: inline-block;
                text-align: center;
                font-size: 0.11rem;
                line-height: 0.15rem;
            }
        }
    }
}
.main {
    margin-top: 0.1rem;
    margin-bottom: 0.44rem;
    img {
        width: 100%;
    }
}
.online-pay {
    width: 100%;
    text-align: center;
    font-size: 0.14rem;
    line-height: 0.44rem;
    background: #bd8747;
    color: #fff;
    position: fixed;
    bottom: 0;
    left: 0;
}
</style>



